import React from 'react';
import './ImageLinkForm.css';

const ImageLinkForm =({onInputChange,onButtonSubmit}) => {
	return (
		<div>
		<p className='f3'>
		{'This Magic Brain will detect faces,age and gender in your pictures.Try it!'}
		</p>
		<div className='center '>
		<div className='form center pa4 br3 shadow-5' >
		 <input className='f4 pa2 w-70 center ma1 bw1 b--dark-gray br4 ' type ='text'
		  onChange={onInputChange}/>
		 <button className='w-30 grow bw0 f4 link ph3 pv2 dib white bg-green'
		 onClick={onButtonSubmit}
		 >Detect</button>
		</div>
		</div> 
		</div>
		);
}

export default ImageLinkForm
